import React, { useState } from "react";

function NewPost({users_id, addProject}) {
  const [name, setName] = useState("")
  const [category, setCategory] = useState("")
  const [user_id, setUser_id] = useState(users_id)
  const [investor_id, setInvestor_id] = useState(1)
  const [description, setDescription] = useState("")
 
  function handleSubmit(e) {
    e.preventDefault();
    
    console.log({
      name,
      category,
      user_id,
      investor_id,
      description,
    });
    
  
    fetch("https://pitch-crest.onrender.com/user_investors", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name: name,
        category: category,
        user_id: user_id,
        investor_id: investor_id,
        description: description,
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        console.log(data);
        if (data.errors) {  
          
          

          console.error("Error:", data.errors);
          alert("Error adding the project.");
        } else if(data.id) {
          addProject(data)
          console.log("Success:", data);
          console.log("Success! Investor ID:", data.id);
          
          setName("");
          setCategory("");
          setUser_id("");
          setInvestor_id(1);
          setDescription("");
        
          alert("Project added successfully!");
        }
      })
      .catch((error) => {
        console.error("Error:", error);
        alert("An error occurred while adding the project.");
      });
  }
  
    return (
      <div className="mb-54 mt-24 border border-blue-500 p-6 rounded-lg text-center" >
        <h2 className="text-xl font-bold mb-4">Add a New Project</h2>

        <form onSubmit={handleSubmit}>
          <div className="mb-6">
            <label htmlFor="text" class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Title</label>
            <input type="text" id="name" value={name} onChange={(e) => setName(e.target.value)}class="shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light" placeholder="title" required />
          </div>
          <div className="mb-6">
            
            <label htmlFor="text" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Category</label>
            <input type="text" id="category" name="category" value={category} onChange={(e) => setCategory(e.target.value)} class="shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light" required />
          </div>
          <div classNameclassName="mb-6">
            <label htmlFor="text" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white"></label>
            <input type="hidden" id="user_id" name="user_id" value={user_id} onChange={(e) => setUser_id(e.target.value)} class="shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light" required />
          </div>
          <div className="mb-6">
            <label htmlFor="text" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white"></label>
            <input type="hidden" id="investor_id" name="investor_id" value={investor_id} onChange={(e) => setInvestor_id(e.target.value)} class="shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light" placeholder="Category" required />
          </div>
          <div className="mb-6">
            <label htmlFor="message" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Description</label>
            <textarea id="description" name="description" rows="4"value={description} onChange={(e) => setDescription(e.target.value)} class="block p-2.5 w-full text-sm text-gray-900 bg-gray-50 rounded-lg border border-gray-300 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Leave a comment..."></textarea>
          </div>
         
          <button type="submit" className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Post New Project</button>
        </form>

      </div>
)}

export default NewPost